export const adaptCommentToClient = (comment) => {
  return {
    "id": comment[`id`],
    "authorId": comment[`user`][`id`],
    "author": comment[`user`][`name`],
    "rating": comment[`rating`],
    "text": comment[`comment`],
    "date": new Date(comment[`date`]),
  };
};

